<template>
	<div class="wrapper">
		<div class="dashboard__header">
			<div class="dashboard__title">
				{{ activeDashboardTitle }}
			</div>
			<div class="dashboard__control">
				<button-vue
					:color="'gray'"
					:active="activeButton === 'create'"
					@click="switchView('create')"
				>
					Создать
				</button-vue>
				<button-vue
					:color="'gray'"
					:active="activeButton === 'list'"
					@click="switchView('list')"
				>
					Список
				</button-vue>
			</div>
		</div>
		<section class="dashboard__section">
			<!-- <div class="dashboard__title">
				{{ activeComponentTitle }}
			</div> -->
			<keep-alive>
				<component
					:is="activeComponent"
					ref="componentRef"
					class="animate__animated animate__fadeIn"
				/>
			</keep-alive>
			<!-- <vue-element-loading 
			:active="dashboardLoading"
			:background-color="'rgba(255, 255, 255, .2)'"
			:color="'blue'"
			/> -->
		</section>
	</div>
</template>

<script>
import ButtonVue from '@/components/BaseButton.vue';
export default {
	components: {
		ButtonVue
	},
	props: {

	},
	data() {
		return {
			activeDashboardTitle: 'Шаблоны клиента 87777777777',
			activeButton: 'create',
			components: {
				templates: {
					create: () => import('@/components/TemplateCreate.vue'),
					list: () => import('@/components/TemplateList.vue'),
				},
			},
			activeComponent: null,
			activeComponentTitle: '',
		};
	},
	created() {
		this.setActiveComponent();
	},
	mounted() {
		
	},
	methods: {
		switchView(route) {
			this.activeButton = route;
			this.setActiveComponent();
		},
		setActiveComponent() {
			const widgetComponents = this.components['templates'];
			const component = widgetComponents[this.activeButton];
			component().then((loadedComponent) => {
				this.activeComponent = loadedComponent.default || loadedComponent;
				this.setActiveComponentTitle();
			});
		},
		setActiveComponentTitle() {
			const widgetTitles = {
				clients: {
					create: 'Создание клиента',
					list: 'Список клиентов',
				},
				reports: {
					create: 'Создание отчета',
					list: 'Список отчетов',
				},
				templates: {
					create: `Создание шаблона ${this.$route.params.userId}`,
					list: `Список шаблонов ${this.$route.params.userId}`,
				},
			};
			this.activeDashboardTitle = widgetTitles['templates'][this.activeButton];
		},
	},
};
</script>

<style lang="scss" scoped>
	.dashboard {
		&__header {
			display: flex;
			justify-content: space-between;
			height: 6rem;
		}
		&__title {
			align-self: center;
			font-weight: 600;
			color: #000000;
		}
		&__control {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__section {
			width: 100%;
			min-height: 70vh;
			border-radius: 9px;
			background-color: #ffffff;
		}
	}
</style>